/*Bootstrap reboot*/
body {
    font-family: 'Roboto', sans-serif;
    color: #52575F;
}

a {
    color: #51A7D8;

}

img {
    max-width: 100%;
}

button,
input,
textarea {
    &:focus {
        outline: none;
    }
}

.container {
    @media(min-width: 1660px) {
        max-width: 1630px;
    }
}

h1,
h2,
h3,
h4,
h5,
h6 {
    color: #3A547B;
    text-align: center;
    font-weight: 700;
}

h2 {
    @media(max-width: 575px) {
        font-size: 22px
    }
}

h3 {
    @media(max-width: 575px) {
        font-size: 19px;
    }
}

h4 {
    @media(max-width: 575px) {
        font-size: 17px;
    }
}

h5 {
    @media(max-width: 575px) {
        font-size: 16px;
    }
}

h6 {
    @media(max-width: 575px) {
        font-size: 15px;
    }
}

/*Header*/
.site-header {
    background: #3A547B;

    @media(min-width: 768px) and (max-width: 1199px) {
        padding: 15px 0;
    }
}

.site-header__row {
    @media(max-width: 767px) {
        padding: 10px 0;
        justify-content: space-between;
    }

    align-items: center;

    @media(min-width: 1200px) {
        min-height: 110px;
    }

}

.site-header__logo-col {
    @media(max-width: 767px) {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 15px;
    }

    @media(min-width:768px) and (max-width: 1199px) {
        flex: 0 0 auto;
        max-width: inherit;
        width: auto;
        margin-right: auto;
    }
}

a.site-header__logo {
    transition: opacity 0.3s ease;

    @media(min-width: 768px) {

        &:hover,
        &:focus {
            opacity: 0.7;
        }
    }


    @media(max-width: 767px) {
        margin-right: 15px;

        &:active {
            opacity: 0.7;
        }
    }
}

.mobile-menu-toogle {
    @media(max-width: 767px) {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        flex-shrink: 0;
        border: 0;
        padding: 0;
        background: transparent;
        width: 36px;
        height: 36px;

        &:active {
            .mobile-menu-toogle__bar {
                background: #123973;
            }
        }
    }
}

.mobile-menu-toogle__bar {
    @media(max-width: 767px) {
        height: 2px;
        width: 26px;
        background: #fff;
        border-radius: 3px;

        &:not(:last-child) {
            margin-bottom: 5px;
        }
    }
}

.site-header__nav-col {
    @media(min-width: 768px) and (max-width: 1199px) {
        margin-top: 20px;
    }

    @media(max-width: 1199px) {
        order: 1;
    }
}

.site-header__main-nav-wrap {
    @media(max-width: 767px) {
        padding-top: 15px;
        margin: 0 -15px;
    }

    ul {
        list-style: none;
        margin-bottom: 0;
        padding-left: 0;
    }

    &>ul {
        display: flex;

        @media(max-width: 767px) {
            flex-direction: column;
        }

        @media(min-width: 1200px) {
            justify-content: center;
        }


        &>li {
            &:not(:last-child) {
                @media(min-width: 768px) and (max-width: 1659px) {
                    margin-right: 30px;
                }

                @media(min-width:1660px) {
                    margin-right: 65px;
                }
            }

            &.site-header__main-nav-item_has-child {
                @media(min-width: 768px) {
                    &:hover {
                        &>ul {
                            display: block;
                        }
                    }
                }

                @media(max-width: 767px) {
                    position: relative;

                    &>a {
                        padding-right: 45px;
                    }
                }

            }

            &.site-header__main-nav-item_active>a {
                @media(max-width: 767px) {
                    background: #123973;
                }

                @media(min-width: 768px) {
                    &:before {
                        opacity: 1;
                    }
                }
            }

            &>a {
                position: relative;
                font-size: 13px;
                color: #fff;
                text-transform: uppercase;

                @media(max-width: 767px) {
                    display: block;
                    padding: 10px 15px;

                    &:hover,
                    &:focus {
                        text-decoration: none;
                    }

                    &:active {
                        background: #123973;
                    }
                }

                &:before {
                    @media(min-width: 768px) {
                        opacity: 0;
                        content: '';
                        position: absolute;


                        left: 0;
                        bottom: -10px;
                        width: 100%;
                        height: 3px;


                        background: #fff;
                        transition: opacity 0.2s ease;
                    }
                }

                @media(min-width: 768px) {

                    &:hover,
                    &:focus {
                        text-decoration: none;

                        &:before {
                            opacity: 1;
                        }
                    }
                }

            }
        }
    }

    &>ul ul {
        @media(min-width: 768px) {
            display: none;
            position: absolute;
            padding-top: 18px;
            z-index: 1;

            &:before {
                content: '';
                position: absolute;
                z-index: -1;
                top: 5px;
                left: 15px;
                border: 7px solid transparent;
                border-bottom-color: #123973;

            }

            li.site-header__main-nav-subitem_active>a {
                background: #4569a0;
            }

            a {
                display: block;
                padding: 10px 15px;
                background: #123973;
                color: #fff;
                transition: background-color 0.1s ease;

                &:hover,
                &:focus {
                    background: #4569a0;
                    text-decoration: none;
                }
            }
        }

        @media(max-width: 767px) {
            display: none;

            li.site-header__main-nav-subitem_active>a {
                background: #123973;
            }

            a {
                display: block;

                padding: 10px 15px 10px 30px;
                position: relative;
                font-size: 13px;
                color: #fff;
                text-transform: uppercase;

                &:hover,
                &:focus {
                    text-decoration: none;
                }

                &:active {
                    background: #123973;
                }
            }
        }
    }
}

.site-header__main-nav-submenu-toggler {
    @media(max-width: 767px) {
        position: absolute;
        right: 0;
        top: 0;
        width: 40px;
        height: 40px;
        background: url(../img/i/shevron-down.svg) no-repeat 50%;
        background-size: 16px auto;
        transition: opacity 0.3s ease;

        &:active {
            opacity: 0.6;
        }
    }
}

.site-header__soclinks-col {
    @media(max-width: 1199px) {
        flex: 0 0 auto;
        max-width: inherit;
        width: auto;
    }

    display: flex;
    justify-content: center;
}

.site-header__soclink {
    display: flex;
    justify-content: center;
    align-items: center;

    @media(max-width: 1199px) {
        width: 30px;
        height: 30px;
    }

    @media(min-width: 1200px) {
        width: 50px;
        height: 50px;
    }

    border-radius: 100%;
    background: #536F99;
    transition: background-color 0.2s ease;

    &:not(:last-child) {
        @media(min-width: 768px) {
            margin-right: 30px;
        }

        @media(max-width: 767px) {
            margin-right: 20px;
        }
    }

    @media(max-width: 767px) {
        &:active {
            background: #7B93B7;
        }
    }

    @media(min-width: 768px) {

        &:hover,
        &:focus {
            background: #7B93B7;
        }
    }
}

.site-header__soclink-i {
    @media(max-width: 1199px) {
        width: 14px;
    }

}

.site-header__lang-col {
    @media(max-width: 1199px) {
        flex: 0 0 auto;
        max-width: inherit;
        width: auto;
    }

    display: flex;
    justify-content: flex-end;
}

.site-header__lang-link {
    position: relative;
    font-weight: 900;
    font-size: 13px;
    text-transform: uppercase;
    color: #FFFFFF;
    transition: opacity 0.3s ease;

    &:before {
        opacity: 0;
        content: '';
        position: absolute;
        left: 0;
        bottom: -2px;
        width: 100%;
        height: 3px;
        background: #fff;
        transition: opacity 0.3s ease;
    }

    &:not(:last-child) {
        margin-right: 20px;
    }

    @media(max-width: 767px) {

        &:not(.site-header__lang-link-active):hover,
        &:not(.site-header__lang-link-active):focus {
            color: #fff;
            text-decoration: none;
        }

        &:not(.site-header__lang-link-active):active {
            opacity: 0.8;

            &:before {
                opacity: 1;
            }
        }
    }

    @media(min-width: 768px) {

        &:not(.site-header__lang-link-active):hover,
        &:not(.site-header__lang-link-active):focus {
            color: #fff;
            text-decoration: none;
            opacity: 0.8;

            &:before {
                opacity: 1;
            }
        }
    }
}

.site-header__lang-link-active {
    &:before {
        opacity: 1;
    }
}

/*Footer*/
.site-footer__top-section-row {

    @media(min-width: 768px) {
        align-items: center;
        min-height: 110px;
    }

    @media(max-width: 767px) {
        padding: 15px 0;
    }
}

.site-footer__top-section {
    background: #3A547B;
}

.site-footer__logo-col {
    @media(min-width: 1660px) {
        flex: 0 0 16.66667%;
        max-width: 16.66667%;
        padding-left: 15px;
    }

    @media(max-width: 1659px) {
        display: none;
    }
}

.site-footer__nav-col {
    @media(min-width: 1660px) {
        flex: 0 0 66.66667%;
        max-width: 66.66667%;
    }

    &>ul {
        display: flex;

        @media(max-width: 767px) {
            flex-direction: column;
        }

        @media(max-width: 991px) {
            flex-wrap: wrap;
        }

        @media(min-width: 1660px) {
            justify-content: center;
        }

        &>li {
            &:not(:last-child) {
                @media(min-width: 1660px) {
                    margin-right: 35px;
                }

                @media(min-width: 768px) and (max-width: 1659px) {
                    margin-right: 15px;
                }

                @media(max-width: 767px) {
                    margin-bottom: 10px;
                }
            }

            &>a {
                color: #fff;

                @media(min-width: 1660px) {
                    font-size: 13px;

                    text-transform: uppercase;
                }

                @media(max-width: 1659px) {
                    font-size: 14px;
                }
            }
        }
    }


    ul {
        list-style: none;
        padding-left: 0;
        margin-bottom: 0;
    }
}

.site-footer__soclinks-col {
    display: flex;
    justify-content: flex-end;

}

.site-footer__soclink {
    &:not(:last-child) {
        margin-right: 30px;
    }

    transition:opacity 0.3s ease;

    &:hover,
    &:focus {
        opacity: 0.6;
    }
}

.site-footer__bottom-section {
    background: #364E72;
}

.site-footer__bottom-section-row {

    @media(min-width: 768px) {
        align-items: center;
        min-height: 50px;
    }

    @media(max-width: 767px) {
        padding: 15px 0;
    }
}

.site-footer__copyright-col {
    font-size: 14px;
    color: #849DAB;

    @media(max-width: 767px) {
        margin-bottom: 20px;
    }
}

.site-footer__developer-col {
    @media(min-width: 768px) {
        display: flex;
        justify-content: flex-end;
    }
}

.site-footer__developer-logo {
    font-size: 12px;
    color: #849DAB;
    display: flex;
    align-items: center;

}

.site-footer__developer-logo-explain-text {
    margin-right: 8px;
}

.site-footer__developer-logo-link {
    display: flex;
    align-items: center;
    transition: opacity 0.3s ease;

    &:hover,
    &:focus {
        opacity: 0.7;
    }
}

.site-footer__developer-logo-name {
    margin-left: 6px;
    color: #849DAB;
    text-decoration: underline;
}

/*Pages*/
.section-title {
    @media(min-width: 768px) {
        font-size: 30px;
    }

    @media(min-width: 576px) and (max-width: 767px) {
        font-size: 25px;
    }

    @media(max-width: 575px) {
        font-size: 20px;
    }

    text-transform: uppercase;
    margin-bottom: 0;
}

.item-centered {
    display: flex;
    justify-content: center;
}

.typical-btn {
    display: inline-flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    min-height: 50px;
    text-transform: uppercase;
    color: #FFFFFF;
    font-weight: 700;

    @media(min-width: 576px) {
        padding: 12px 30px;
    }

    @media(max-width: 575px) {
        font-size: 15px;
        padding: 12px 15px;
    }

    background:#51A7D8;
    border-radius:10px;
    transition: background 0.3s ease,
    box-shadow 0.3s ease;
    border: 0;

    &:hover,
    &:focus {
        background: #1D88C6;
        color: #fff;
        text-decoration: none;
    }

    &:active {
        background: #3A547B;
        box-shadow: inset 0px 0px 5px 0px rgba(50, 50, 50, 0.75);
    }
}

.typical-btn__i {
    flex-shrink: 0;
    margin-right: 7px;
}

.form-agreement {
    font-size: 14px;

    input {
        display: none;

        &:checked+label {
            border-color: #536F99;

            &:before {
                opacity: 1;
            }
        }
    }

    label {
        position: relative;
        width: 20px;
        height: 20px;
        background: #FFFFFF;
        border: 1px solid #D9DADC;
        border-radius: 5px;
        margin: 0 4px 0 0;
        transition: border-color 0.2s ease;
        vertical-align: -4px;
        cursor: pointer;

        &:before {
            content: '';
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            width: 6px;
            height: 6px;
            border-radius: 100%;
            background: #536F99;
            opacity: 0;
            transition: opacity 0.2s ease;
        }
    }
}

.profile-form-section {
    background: #eff3f8;

    @media(min-width: 1200px) {
        padding: 130px 0;
    }

    @media(min-width: 768px) and (max-width: 1199px) {
        padding: 50px 0;
    }

    @media(max-width: 767px) {
        padding: 30px 0;
    }
}

.profile-form-section__section-title {
    @media(min-width: 768px) {
        margin-bottom: 30px;
    }

    @media(max-width: 767px) {
        margin-bottom: 15px;
    }
}

.profile-form__col {
    &:not(:last-child) {
        @media(max-width: 767px) {
            margin-bottom: 30px;
        }
    }
}

.profile-form-section_home {
    @media(min-width: 1200px) {
        margin-bottom: 130px;
    }

    @media(min-width: 768px) and (max-width: 1199px) {
        margin-bottom: 50px;
    }

    @media(max-width: 767px) {
        margin-bottom: 30px;
    }
}

.profile-form-section__intro-text {
    text-align: center;

    p:last-child {
        margin-bottom: 0;
    }

    @media(min-width: 1660px) {
        margin-bottom: 100px;
    }

    @media(min-width: 768px) and (max-width: 1659px) {
        margin-bottom: 50px;
    }

    @media(max-width: 767px) {
        margin-bottom: 20px;
    }
}

.profile-form-section__content {
    @media(min-width: 1660px) {
        margin-left: 25%;
        max-width: 50%;
    }
}

.profile-form__formgroup {
    &:not(:last-child) {
        margin-bottom: 30px;
    }
}

.profile-form__field {
    height: 50px;
    border-color: #D9DADC;
    padding: 10px 20px;
    color: #333;

    &::placeholder {
        opacity: 1;
        color: #536F99;
    }
}

.profile-form__age-wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.profile-form__age-title {
    margin-right: 20px;
    color: #536F99;
}

.profile-form__age-wrap-field {
    width: 80px;
}

.profile-form__field.profile-form__field_message {
    @media(min-width: 768px) {
        height: 370px;
    }

    @media(max-width: 767px) {
        height: 120px;
    }

    max-width: 100%;
}

.profile-form__language-agreement-row {
    @media(min-width: 1200px) {
        margin: 30px 0 70px;
    }

    @media(max-width: 1199px) {
        margin: 20px 0 40px;
    }

    display: flex;

    @media(min-width: 768px) {
        align-items: flex-start;
    }

    @media(max-width: 767px) {
        flex-direction: column;
    }
}

.profile-form__language-text {
    color: #536F99;

    @media(min-width: 576px) {
        margin-right: 20px;
        white-space: nowrap;
    }

    @media(max-width: 575px) {
        width: 100%;
        margin-bottom: 15px;
    }
}

.profile-form__language-select-wrap {
    display: flex;
    align-items: center;

    @media(min-width: 768px) {
        margin-right: 40px;
    }

    @media(max-width: 767px) {
        margin-bottom: 20px;
    }

    @media(max-width: 575px) {
        flex-wrap: wrap;
    }
}

.profile-form__language-item {
    display: flex;
    align-items: center;

    &:not(:last-child) {
        margin-right: 30px;
    }

    input {
        display: none;

        &:checked+label {
            color: #273A55;
            border-color: #536F99;

            &:after {
                opacity: 1;
            }
        }
    }

    label {
        color: #536F99;
        position: relative;
        display: flex;

        &:before {
            content: '';
            width: 20px;
            height: 20px;
            background: #FFFFFF;
            border: 1px solid #D9DADC;
            border-radius: 5px;
            margin-right: 10px;
        }

        margin:0;
        transition:all 0.2s ease;
        cursor: pointer;

        &:after {
            content: '';
            position: absolute;
            top: 7px;
            left: 7px;
            width: 6px;
            height: 6px;
            border-radius: 100%;
            background: #536F99;
            opacity: 0;
            transition: opacity 0.2s ease;
        }
    }
}

.about-us-section {
    *:last-child {
        margin-bottom: 0;
    }
}

.about-us-section_home {
    @media(min-width: 1200px) {
        margin-bottom: 130px;
    }

    @media(min-width: 768px) and (max-width: 1199px) {
        margin-bottom: 50px;
    }

    @media(max-width: 767px) {
        margin-bottom: 30px;
    }
}

.about-us-section__section-title {
    @media(min-width: 1200px) {
        margin-bottom: 50px;
    }

    @media(min-width: 768px) and (max-width: 1199px) {
        margin-bottom: 30px;
    }

    @media(max-width: 767px) {
        margin-bottom: 20px;
    }
}

.about-us-section__content {
    @media(min-width: 1660px) {
        margin-left: 25%;
        max-width: 50%;
    }
}

.about-us-section__marked-paragraph {
    font-weight: 500;

    @media(min-width: 768px) {
        font-size: 24px;
        line-height: 1.28;
        text-align: center;
    }

    color: #51A7D8;

    @media(min-width: 768px) and (max-width: 1199px) {
        margin: 30px 0;
    }

    @media(min-width: 1200px) {
        margin: 70px 0;
    }

    @media(max-width: 767px) {
        margin-bottom: 15px;
    }

}

.our-principles-section {
    @media(min-width: 1660px) {
        padding: 130px 0;
    }

    @media(min-width: 1200px) and (max-width: 1659px) {
        padding: 80px 0 100px;
    }

    @media(min-width: 768px) and (max-width: 1199px) {
        padding: 50px 0;
    }

    @media(max-width: 767px) {
        padding: 30px 0;
    }

    background:url(../img/principles-bg.jpg) no-repeat 50% 0;
    background-size:cover;
}

.our-principles-section__col {
    @media(max-width: 767px) {
        &:not(:last-child) {
            margin-bottom: 40px;
        }
    }

    @media(min-width: 768px) and (max-width: 1199px) {
        &:not(:last-child):not(:nth-last-child(2)) {
            margin-bottom: 40px;
        }
    }
}

.our-principles-section__section-title {
    color: #fff;

    @media(min-width: 768px) and (max-width: 1199px) {
        margin-bottom: 30px;
    }

    @media(min-width: 1200px) {
        margin-bottom: 80px;
    }

    @media(max-width: 767px) {
        margin-bottom: 25px;
    }
}

.our-principles-section__principle-item {
    position: relative;

    @media(min-width: 768px) {
        height: 100%;
    }

    background:#fff;
    border-radius: 10px;

    @media(min-width: 1660px) {
        padding: 50px 50px 220px 50px;
    }

    @media(min-width:768px) and (max-width: 1659px) {
        padding: 20px 20px 220px 20px;
    }

    @media(max-width:767px) {
        padding: 20px 20px 220px 20px;
    }
}

.our-principles-section__principle-title {
    font-weight: 500;

    @media(max-width: 1659px) {
        font-size: 22px;
        margin-bottom: 15px;
    }

    @media(min-width: 1660px) {
        font-size: 24px;
        margin-bottom: 30px;
    }

    text-align: center;
    color: #536F99;

}

.our-principles-section__principle-text {
    p:last-child {
        margin-bottom: 0;
    }
}

.our-principles-section__principle-i {
    position: absolute;
    left: 50%;
    transform: translate(-50%, 0);
    bottom: 50px;
}

.contacts-us-section {
    background: #eff3f8;

    @media(min-width: 1200px) {
        padding: 130px 0 110px;
    }

    @media(min-width: 768px) and (max-width: 1199px) {
        padding: 50px 0;
    }

    @media(max-width: 767px) {
        padding: 30px 0;
    }
}

.contacts-us-section__section-title {
    @media(min-width: 1200px) {
        margin-bottom: 80px;
    }

    @media(min-width: 768px) and (max-width: 1199px) {
        margin-bottom: 40px;
    }

    @media(max-width: 767px) {
        margin-bottom: 25px;
    }
}

.contacts-us-section__content-wrap {
    @media(min-width: 576px) {
        max-width: 380px;
        margin: 0 auto;
    }
}

.contacts-us-section__content-item {
    display: flex;
    align-items: center;

    @media(max-width: 575px) {
        justify-content: center;
    }

    @media(min-width: 576px) {
        border-bottom: 1px solid #D9DADC;
        padding: 0 15px 10px;
        font-size: 22px;
    }

    &:not(:last-child) {
        @media(min-width: 576px) {
            margin-bottom: 40px;
        }

        @media(max-width: 575px) {
            margin-bottom: 20px;
        }
    }

    a {
        color: #52575F;

        &:hover,
        &:focus {
            color: #3A547B;
        }
    }
}

.contacts-us-section__content-item-i {
    flex-shrink: 0;

    @media(min-width: 576px) {
        margin-right: 20px;
    }

    @media(max-width: 575px) {
        margin-right: 7px;
        max-width: 18px;
    }
}